.complete {
  max-width: rem(800);
  width: 100%;
  min-height: 100vh;
  margin-right: auto;
  margin-left: auto;
  padding: rem(60) rem(15);
  &__title {
    font-size: rem(22);
    font-weight: bold;
    text-align: center;
    @include mq("sp") {
      font-size: rem(18);
    }
  }
  &__text {
    font-size: rem(16);
    font-weight: bold;
    text-align: center;
    margin-top: rem(60);
  }
  &__btnWrap {
    margin-top: rem(80);
  }
}